                        {{
                          model.meetingJoinUrl && model.meetingJoinUrl.length
                            ? "Zoom"
                            : "Add Zoom"
                        }}
                      </span>
                      <span>cancel Zoom</span>
                    </button>
                    <a
                      :href="model.meetingJoinUrl"
                      target="_blank"
                      class="btn-zoom-link"
                      v-tooltip="'Open the meeting link'"
                      v-if="model.meetingJoinUrl && model.meetingJoinUrl.length > 0"
                    >
                      <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><g clip-path="url(#clip0)" fill="#909DB1"><path d="M18.627 1.373a4.688 4.688 0 00-6.63 0L8.08 5.29c-1.893 1.893-1.718 4.91 0 6.629.288.287.606.522.942.715l.716-.715c.469-.47.303-1.018.295-1.413a2.265 2.265 0 01-.295-.245c-.882-.881-.922-2.393 0-3.314l3.917-3.918a2.346 2.346 0 013.315 0c.914.914.914 2.4 0 3.315l-2.59 2.59c.075.414.524 1.395.293 3.014.012-.01.025-.018.036-.03l3.918-3.917a4.688 4.688 0 000-6.629z"/><path d="M12.223 7.777a4.46 4.46 0 00-.942-.715l-.715.715c-.469.469-.304 1.017-.295 1.413.103.073.203.151.295.244.882.882.921 2.393 0 3.314L6.345 16.97c-.914.914-2.4.914-3.315 0a2.346 2.346 0 010-3.315l2.893-2.893c-.074-.414-.524-1.396-.293-3.015-.011.011-.025.018-.036.03l-4.221 4.22a4.688 4.688 0 006.629 6.63l4.221-4.221c1.859-1.859 1.757-4.873 0-6.63z"/></g><defs><clipPath id="clip0"><path fill="#fff" d="M0 0h20v20H0z"/></clipPath></defs></svg>
                    </a>
                  </div>
                  <div
                    class="zoom-link"
                    v-if="model.meetingJoinUrl && model.meetingJoinUrl.length > 0"
                    v-tooltip="'Click to copy'"
                    @click="copyZoomLinkToClipboard"
                  >
                    <span>
                      {{ model.meetingJoinUrl }}
                    </span>
                    <small class="zoom-link__icon">
                      <svg width="11" height="11" viewBox="0 0 11 11" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" clip-rule="evenodd" d="M7.933 1.731h-3.2a.384.384 0 00-.373.394v.563h.907c.589 0 1.066.503 1.066 1.124v3.77h1.6a.384.384 0 00.374-.394V2.125a.384.384 0 00-.374-.394zm-1.6 6.582h1.6C8.523 8.313 9 7.809 9 7.187V2.125C9 1.504 8.522 1 7.933 1h-3.2c-.589 0-1.066.504-1.066 1.125v.563h-1.6C1.477 2.688 1 3.19 1 3.812v5.063C1 9.496 1.478 10 2.067 10h3.2c.589 0 1.066-.504 1.066-1.125v-.563zm-4.64-4.5c0-.218.167-.394.374-.394h3.2c.206 0 .373.176.373.393v5.063a.384.384 0 01-.373.394h-3.2a.384.384 0 01-.374-.394V3.812z" fill="#2889FC"/></svg>
                    </small>
                  </div>
                </div>
              </div>

              <div class="w-100 row-fields">
                <div class="form-wrap__long__column description-column">
                  <span class="fs-14 mb-5 color-gray-secondary field-label">
                    Description
                  </span>
                  <div v-if="allowRichEditor" class="new_event-editor">
                    <rich-editor
                      @change="inputChenged = true"
                      v-model="model.description"
                    />
                  </div>
                  <c-textarea
                    v-else
                    class="w-100"
                    height="115px"
                    v-model="model.description"
                    placeholder="Description"
                  />
                </div>
                <div class="form-wrap__column-third image-column pc">
                  <span class="fs-14 mb-5 color-gray-secondary field-label">
                    Image
                    <template v-if="model.id"
                      >(<span @click="isEdit = !isEdit">edit</span>)</template
                    >
                  </span>
                  <!--IMAGE-->
                  <div class="group-single__info-image w-100">
                    <img
                      :src="model.photo.url"
                      v-if="!isEdit && model.photo && model.photo.url"
                    />
                    <template v-if="uploadedImg">
                      <img :src="'data:image/png;base64, ' + uploadedImg.binary" />
                    </template>
                    <c-image-upload-simple
                      @ready="changePhoto"
                      @deleteImage="deleteImage"
                      :hasPhoto="!!editedImgURL || !!uploadedImg || !!model.photo"
                    />
                  </div>
                </div>
              </div>

              <div class="mt-20 w-100 text-right modal-actions">
                <c-button
                  variant="plain"
                  tag="button"
                  @click="$store.dispatch('setActiveModal', null)"
                  size="sm"
                  class="button-cancel fs-16"
                >
                  Cancel
                </c-button>
                <div class="modal-actions__submit">
                  <c-button
                    tag="button"
                    class="ml-auto"
                    size="sm"
                    variant="outline-info"
                    @click="createEvent()"
                    v-if="!model.id"
                  >
                    Save to drafts
                  </c-button>
                  <c-button
                    variant="info"
                    tag="button"
                    size="sm"
                    :disabled="isFormChange(invalid, dirty)"
                    @click="settingsToggleFun()"
                    v-if="model.id"
                  >
                    {{ model.id ? "Save" : "Publish" }}
                  </c-button>
                </div>
              </div>
            </ValidationObserver>
          </div>

          <div
            class="modal-tabs__content-tab"
            v-if="this.model.id"
            :class="{ active: activeEditModalTab == 1 }"
          >
            <!--PARTICIPANTS TABLE-->
            <vue-good-table
              class="custom-table custom-table-attendees"
              :columns="columnsParticipants"
              :rows="rowsParticipants"
              :pagination-options="{
                enabled: tablesPaginationEnable,
                prevLabel: '',
                nextLabel: '',
                perPage: 100,
              }"
            >
              <template slot="table-row" slot-scope="props">
                <div class="user-name-col" v-if="props.column.field === 'firstName'">
                  <span v-if="props.row.photo" class="user-name-col__image">
                    <img :src="props.row.photo.url" alt="" />
                  </span>
                  <span v-else class="user-name-col__image">
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 480 480" v-bind:svg-inline="''" v-bind:height="'30px'" v-bind:width="'30px'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M240 0C107.664 0 0 107.664 0 240c0 57.96 20.656 111.184 54.992 152.704.088.12.096.272.192.384 24.792 29.896 55.928 52.816 90.624 67.624.4.168.792.352 1.192.52 2.808 1.184 5.648 2.28 8.496 3.352 1.12.424 2.24.856 3.376 1.264 2.456.88 4.928 1.712 7.416 2.512 1.592.512 3.184 1.016 4.792 1.496 2.2.656 4.408 1.288 6.632 1.888 1.952.528 3.92 1.016 5.888 1.488 1.992.48 3.992.96 6 1.384 2.24.48 4.504.904 6.776 1.32 1.824.336 3.64.688 5.48.984 2.52.408 5.056.728 7.6 1.056 1.64.208 3.272.448 4.92.624 2.88.304 5.784.52 8.696.72 1.352.096 2.696.24 4.056.312 4.248.24 8.544.368 12.872.368s8.624-.128 12.888-.352c1.36-.072 2.704-.216 4.056-.312 2.912-.208 5.816-.416 8.696-.72 1.648-.176 3.28-.416 4.92-.624 2.544-.328 5.08-.648 7.6-1.056 1.832-.296 3.656-.648 5.48-.984 2.264-.416 4.528-.84 6.776-1.32 2.008-.432 4-.904 6-1.384 1.968-.48 3.936-.968 5.888-1.488a232.661 232.661 0 0011.424-3.384c2.488-.8 4.96-1.632 7.416-2.512 1.128-.408 2.248-.84 3.376-1.264a243.168 243.168 0 008.496-3.352c.4-.168.792-.352 1.192-.52a239.997 239.997 0 0090.624-67.624c.096-.112.104-.272.192-.384C459.344 351.184 480 297.96 480 240 480 107.664 372.336 0 240 0zm97.256 441.76c-.12.056-.232.12-.352.176-2.856 1.376-5.76 2.672-8.688 3.936-.664.28-1.32.568-1.984.848a217.023 217.023 0 01-7.76 3.064 159.85 159.85 0 01-3.272 1.192 230.572 230.572 0 01-6.976 2.368c-1.456.464-2.92.904-4.384 1.336a202.08 202.08 0 01-6.28 1.784c-1.776.472-3.568.904-5.36 1.328-1.88.448-3.752.904-5.648 1.304-2.072.44-4.16.816-6.24 1.192-1.688.312-3.368.64-5.072.912-2.344.368-4.712.664-7.072.96-1.496.192-2.984.416-4.496.576-2.696.288-5.416.472-8.128.664-1.208.08-2.408.216-3.632.28-3.96.208-7.928.32-11.912.32s-7.952-.112-11.904-.32c-1.216-.064-2.416-.192-3.632-.28-2.72-.184-5.432-.376-8.128-.664-1.512-.16-3