                  :name="this.model.title + '-attendees.xls'"
                  class="c-button px-15 c-button--sm c-button--outline-info button-download-attendees"
                >
                  Download
                </download-excel>
              </span>
            </div>
          </div>

          <div
            class="modal-tabs__content-tab"
            v-if="this.model.id"
            :class="{ active: activeEditModalTab == 2 }"
