-.16-3-.384-4.496-.576-2.36-.296-4.728-.592-7.072-.96-1.704-.272-3.384-.6-5.072-.912-2.088-.376-4.176-.76-6.24-1.192-1.896-.4-3.776-.856-5.648-1.304-1.792-.432-3.584-.856-5.36-1.328-2.104-.56-4.2-1.168-6.28-1.784a212.543 212.543 0 01-4.384-1.336 223.256 223.256 0 01-6.976-2.368 217.023 217.023 0 01-11.032-4.256c-.664-.272-1.312-.56-1.976-.84a223.812 223.812 0 01-8.696-3.936 7.216 7.216 0 01-.352-.176c-27.912-13.504-52.568-32.672-72.576-55.952 15.464-56.944 59.24-102.848 115.56-121.112 1.112.68 2.272 1.288 3.416 1.928.672.376 1.336.776 2.016 1.136a99.592 99.592 0 007.272 3.512c1.896.832 3.856 1.536 5.808 2.256.384.136.768.288 1.152.424 10.848 3.84 22.456 6.04 34.6 6.04s23.752-2.2 34.592-6.04c.384-.136.768-.288 1.152-.424 1.952-.72 3.912-1.424 5.808-2.256a99.592 99.592 0 007.272-3.512c.68-.368 1.344-.76 2.016-1.136 1.144-.64 2.312-1.248 3.432-1.936 56.32 18.272 100.088 64.176 115.56 121.112-20.008 23.272-44.664 42.44-72.576 55.952zM152 176c0-48.52 39.48-88 88-88s88 39.48 88 88c0 30.864-16.008 58.024-40.128 73.736a84.742 84.742 0 01-9.8 5.48c-.4.192-.792.392-1.192.576-23.168 10.536-50.592 10.536-73.76 0-.4-.184-.8-.384-1.192-.576a84.083 84.083 0 01-9.8-5.48C168.008 234.024 152 206.864 152 176zm269.832 194.584c-18.136-53.552-59.512-96.832-112.376-117.392C330.6 234.144 344 206.64 344 176c0-57.344-46.656-104-104-104s-104 46.656-104 104c0 30.64 13.4 58.144 34.552 77.192-52.864 20.568-94.24 63.84-112.376 117.392C31.672 333.792 16 288.704 16 240 16 116.488 116.488 16 240 16s224 100.488 224 224c0 48.704-15.672 93.792-42.168 130.584z"/></svg>
                        </span>
                      </span>
                    </span>
                    <span class="qa-col__head-time">
                      {{ props.row.createdOnUtc }}
                    </span>
                    <div
                      class="qa-col__head-toggle"
                      @click="toggleQA(props.row.originalIndex)"
                    >
                      <svg width="10" height="6" viewBox="0 0 10 6" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M1 1l4 4 4-4" stroke="#909DB1"/></svg>
                    </div>
                  </div>
                  <div class="qa-col__list">
                    <div class="qa-col__list-item">
                      <div class="question">
                        <div class="question__icon mr-15">
                          <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:height="'16px'" v-bind:width="'16px'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" clip-rule="evenodd" d="M2 1.2h12a.8.8 0 01.8.8v12a.8.8 0 01-.8.8H2a.8.8 0 01-.8-.8V2a.8.8 0 01.8-.8zM0 2a2 2 0 012-2h12a2 2 0 012 2v12a2 2 0 01-2 2H2a2 2 0 01-2-2V2zm7.135 7.822c-.051.376.262.688.641.688.375 0 .667-.307.742-.674.081-.395.24-.717.477-.967l.806-.761c.8-.799 1.199-1.609 1.199-2.43 0-.832-.262-1.485-.784-1.96C9.697 3.24 8.973 3 8.04 3c-.897 0-1.627.235-2.19.705-.38.317-.632.703-.758 1.158-.1.365.218.68.596.68.376 0 .655-.325.834-.655.075-.137.176-.26.305-.366.306-.254.71-.38 1.213-.38.518 0 .913.14 1.184.422.271.277.407.662.407 1.155 0 .512-.207.991-.621 1.438l-.97.95c-.38.395-.63.755-.754 1.079-.068.177-.118.389-.151.636zm.092 1.917a.74.74 0 00-.2.528.74.74 0 00.2.529c.138.136.34.204.606.204.267 0 .469-.068.607-.204a.725.725 0 00.207-.529.725.725 0 00-.207-.528c-.138-.146-.34-.219-.607-.219-.266 0-.468.073-.606.219z" fill="#909DB1"/></svg>
                        </div>
                        <div class="question__text">
                          {{ props.row.question }}
                          <div class="question__text__date">
                            {{ props.row.createdOnUtc }}
                          </div>
                        </div>
                      </div>
                      <div class="answer">
                        <div
                          class="answer__icon mr-15"
                          :class="{
                            waitanswer: props.row.editable && !props.row.answer,
                          }"
                        >
                          <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:height="'16px'" v-bind:width="'16px'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" clip-rule="evenodd" d="M14 1.2H2a.8.8 0 00-.8.8v12a.8.8 0 00.8.8h12a.8.8 0 00.8-.8V2a.8.8 0 00-.8-.8zM2 0a2 2 0 00-2 2v12a2 2 0 002 2h12a2 2 0 002-2V2a2 2 0 00-2-2H2zm5.027 12.267a.74.74 0 01.2-.528c.138-.146.34-.219.607-.219.266 0 .468.073.606.219.139.14.208.317.208.528 0 .212-.07.388-.208.529-.138.136-.34.204-.606.204-.267 0-.469-.068-.607-.204a.74.74 0 01-.2-.529zM7.9 3a.7.7 0 00-.7.7v6.1a.7.7 0 101.4 0V3.7a.7.7 0 00-.7-.7z" fill="#909DB1"/></svg>
                        </div>
                        <div class="answer__content w-100">
                          <article v-if="props.row.answer && !props.row.editable">
                            {{ props.row.answer }}
                            <c-button
                              variant="plain"
                              class="ml-5 edit-btn"
                              @click="toggleEditAnswer(props.row.id)"
                              tag="button"
                            >
                              <svg width="22" height="22" viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:height="'16px'" v-bind:width="'16px'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" clip-rule="evenodd" d="M19.022.137a4.018 4.018 0 00-3.657.832l-.225.208-13.3 13.3c-.152.152-.261.34-.317.547l-1.478 5.42c-.251.918.593 1.762 1.512 1.511l5.419-1.478c.206-.056.395-.165.546-.317l13.301-13.3A4.018 4.018 0 0019.022.137zm.06 4.98l-13.07 13.07-3.024.825.825-3.024 13.07-13.07a1.555 1.555 0 012.198 2.2z" fill="#C7CDD8"/></svg>
                            </c-button>
                          </article>
                          <form
                            v-if="props.row.editable"
                            class="w-100 form-answer"
                            @submit.prevent="
                              handleAnswerSubmit(props.row.id, props.row.originalIndex)
                            "
                          >
                            <input
                              placeholder="reply"
                              type="text"
                              name="field_answer"
                              v-model="
                                eventQuestionsAnswers[props.row.originalIndex].answer
                              "
                              required
                            />
                            <button type="submit">
                              <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:height="'17px'" v-bind:width="'17px'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" clip-rule="evenodd" d="M14.694.709c.15-.15-.007-.4-.208-.33L.507 5.27a.5.5 0 00-.038.929l5.732 2.547a.5.5 0 00.557-.103L14.694.71zM7.356 9.242a.5.5 0 00-.103.557L9.8 15.53a.5.5 0 00.93-.038l4.892-13.979c.07-.2-.18-.358-.33-.207L7.355 9.242z" fill="#fff"/></svg>
                            </button>
                          </form>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </template>
            </vue-good-table>
            <span
              class="cancel-mobile-only"
              @click="$store.dispatch('setActiveModal', null)"
            >
              Cancel
            </span>
          </div>
        </div>
      </div>
    </div>
    <!--MODALS-->

    <transition name="fade" mode="out-in" v-if="settingsToggle">
      <c-event-setting-modal
        :type="editDialog"
        @save="createEvent"
        @cancel="settingsToggle = false"
      />
    </transition>
  </div>
</template>

<script>
import { VueGoodTable } from "vue-good-table";
import "vue-good-table/dist/vue-good-table.css";
import JsonExcel from "vue-json-excel";

export default {
  name: "newEvent",
  components: {
    "vue-good-table": VueGoodTable,
    downloadExcel: JsonExcel,
    "c-image-upload-simple": () => import("@/components/image-upload-simple"),
    "rich-editor": () => import("@/components/rich-editor"),

    /* -------------------------- event setting dialog -------------------------- */
    "c-event-setting-modal": () => import("@/components/modals/eventSetting"),
  },
  data() {
    return {
      isEdit: false,
      editDialog: true,
      settingsToggle: false,
      activeToggle: null,
      activeEditModalTab: 0,
      eventTabs: ["Details", "Attendees", "Q&A"],
      currentUserID: 0,
      waitingForAnswer: false,
      allowRichEditor: true,
      endDateChangedManually: false,
      model: {
        startDateTime: new Date(),
        endDateTime: new Date(),
        title: "",
        description: "",
        coverImage: "",
        price: "",
        location: "",
        groupId: "",
        isPublic: true,
        minAttendeesCount: "",
        maxAttendeesCount: "",
        recurrence: {
          name: "",
          value: "",
        },
        recurringData: {},
        meetingJoinUrl: null,
      },
      columnsParticipants: [
        {
          label: "Name",
          field: "firstName",
        },
        {
          label: "Email",
          field: "email",
        },
        {
          label: "Guests",
          field: "attendeesCount",
        },
      ],
      rowsParticipants: [],
      columnsQA: [
        {
          label: "",
          field: "userphoto",
        },
        {
          label: "",
          field: "questionsanswers",
        },
      ],
      rowsQA: [],
      eventQuestionsAnswers: "",
      cronIntervalsTypes: [
        { name: "Does not repeat", value: "0" },
        { name: "Daily", value: "1" },
        { name: "Weekly", value: "2" },
        { name: "Monthly", value: "3" },
        { name: "Every weekday (MON-FRI)", value: "4" },
      ],
      cronExpression: "",
      exportData: {
        json_fields: {
          Name: "firstName",
          Email: "email",
          Guests: "attendeesCount",
        },
        json_meta: [
          [
            {
              key: "charset",
              value: "utf-8",
            },
          ],
        ],
      },
      attendeesCount: 0,
      attendeesTicketsCount: 0,
      disabledBefore: new Date(),
      showPreload: true,
      uploadedImg: null,
      windowWidth: window.innerWidth,
      tablesPaginationEnable: true,
      startDateInvalid: false,
      endDateInvalid: false,
      editedImgURL: null,
      inputChenged: false,
      imageChenged: false,
    };
  },
  computed: {
    isDesktop() {
      return true;
    },
  },

  methods: {
    settingsToggleFun() {
      if (this.model.recurrence.value !== 0) {
        this.settingsToggle = true;
      } else {
        this.createEvent();
      }
    },
    async createEvent(val) {
      let datesAreValid = this.validateDateInputs();

      const valid = await this.$refs.eventObserver.validate();
      if (!valid) {
        return false;
      }

      if (!datesAreValid) {
        return false;
      }

      let url, text, method;
      if (this.model.id) {
        if (val) {
          url = ["EVENT_UPDATE_RECURRING", val];
        } else {
          url = "EVENT_UPDATE";
        }
        text = "Event has been updated!";
        method = "PUT";
      } else {
        url = "EVENT_CREATE";
        text = "Event has been created!";
        method = "POST";
        this.model.groupId = this.$route.params.id;
      }

      const startDate = this.$moment(this.model.startDateTime).format("MM/DD/YYYY HH:mm");
      const endDate = this.$moment(startDate).add(1, "years").format("MM/DD/YYYY HH:mm");

      const recurrenceType = parseInt(this.model.recurrence.value);

      if (recurrenceType !== 0) {
        this.model.recurringData = {
          cronExpression: this.cronExpression,
          fromDate: startDate,
          toDate: endDate,
          recurrenceType: recurrenceType,
          timezoneOffset: new Date().getTimezoneOffset(),
        };
      } else {
        this.model.recurringData = null;
      }

      this.setLoadingStatus(true);
      console.log(url + "?changeType=1");
      this.makeRequest(url, method, this.model).then((res) => {
        if (res.status === 200 && this.$route.params.id) {
          if (this.uploadedImg) {
            this.uploadEventPhoto(this.uploadedImg, res.data.id)
              .then(() => {
                this.loadGroup(this.$route.params.id).then((response) => {
                  if (response && response.status === 200) {
                    this.setLoadingStatus(false);
                    this.$notify({
                      group: "system",
                      title: "Great!",
                      text,
                      type: "success",
                    });
                    this.$store.dispatch("setActiveModal", null);
                  }
                });
              })
              .catch((error) => {
                this.setLoadingStatus(false);
                this.resolveError(error);
                this.loadEventInternal(res.data.id);
                this.uploadedImg = null;
                this.loadGroup(this.$route.params.id);
              });
          } else if (this.imageChenged) {
            const url = `/event/${this.model.id}/photo`;
            this.deleteImg(url, this.model).then((response) => {
              if (response && response.status === 200) {
                this.setLoadingStatus(false);
                this.$notify({
                  group: "system",
                  title: "Great!",
                  text,
                  type: "success",
                });
                this.$store.dispatch("setActiveModal", null);
              }
            });
          } else {
            this.loadGroup(this.$route.params.id).then((response) => {
              if (response && response.status === 200) {
                this.setLoadingStatus(false);
                this.$notify({
                  group: "system",
                  title: "Great!",
                  text,
                  type: "success",
                });
                this.$store.dispatch("setActiveModal", null);
              }
            });
          }
        }
      });
    },
    loadEventInternal(id) {
      this.loadEvent(id).then((response) => {
        if (response && response.status === 200) {
          this.model = { ...response.data };
          this.model.startDateTime = new Date(
            this.$moment.utc(this.model.startDateTime).local().format()
          );
          this.model.endDateTime = new Date(
            this.$moment.utc(this.model.endDateTime).local().format()
          );

          if (this.model.recurringData) {
            const type = this.model.recurringData.recurrenceType;
            const recurrence = this.cronIntervalsTypes[type];
            this.cronExpression = this.model.recurringData.cronExpression;
            if (recurrence) this.model.recurrence = recurrence;
          } else {
            this.cronExpression = "";
            this.model.recurrence = this.cronIntervalsTypes[0];
          }

          this.loadEventParticipantsInternal(id);
          this.loadEventQAInternal(id);
          this.handleDates(this.model.startDateTime, this.model.endDateTime);

          this.showPreload = false;
          console.log(
            "loadEventInternal",
            this.model.id,
            this.model.occurenceIndex,
            this.model
          );
        }
      });
    },
    loadEventParticipantsInternal(id) {
      this.loadEventParticipants(id).then((response) => {
        if (response && response.status === 200) {
          let ticketsCount = 0;
          this.rowsParticipants = response.data.users.map((user) => {
            user.firstName = user.firstName + " " + user.lastName;
            ticketsCount += user.attendeesCount;
            return user;
          });

          this.attendeesCount = this.rowsParticipants.length;
          this.attendeesTicketsCount = ticketsCount;
        }
      });
    },
    loadEventQAInternal(id) {
      this.loadEventQA(id).then((response) => {
        if (response && response.status === 200) {
          this.eventQuestionsAnswers = response.data.questionAndAnswers.map((item) => {
            item.createdOnUtc = this.$moment(item.createdOnUtc).format(
              "MM/DD/YYYY HH:mm"
            );
            item.editable = false;
            if (!item.answer) {
              item.editable = true;
              this.waitingForAnswer = true;
            }
            return item;
          });

          this.rowsQA = [...this.eventQuestionsAnswers];
        }
      });
    },
    handleAnswerSubmit(questionID, rowID) {
      const data = {
        eventId: this.model.id,
        question: "",
        answer: this.eventQuestionsAnswers[rowID].answer,
        parentId: 0,
        occurenceIndex: 0,
        id: questionID,
        apiStatus: 0,
        apiMessage: "",
        createdOnUtc: new Date(),
        updatedOnUtc: new Date(),
        createdById: this.currentUserID,
        updatedById: this.currentUserID,
      };
      this.setLoadingStatus(true);

      this.makeRequest("PUT_EVENT_ANSWER", "PUT", data)
        .then((response) => {
          this.setLoadingStatus(false);
          if (response.status === 200 && this.$route.params.id) {
            this.toggleEditAnswer(questionID);
          }
        })
        .catch(() => {});
    },
    changePhoto(img) {
      this.imageChenged = true;
      this.editedImgURL = img;
      this.uploadedImg = null;
      this.uploadedImg = img;
    },
    uploadEventPhoto(img, eventID = null) {
      if (eventID) {
        const url = `/event/${eventID}/photo`;
        return this.uploadImgWithRezult(url, img);
      } else if (this.model.id) {
        const url = `/event/${this.model.id}/photo`;
        this.uploadImg(url, img).then((response) => {
          if (response && response.status === 200) {
            console.log("uploadEventPhoto", response.data);
            this.editedImgURL = response.data.url;
            this.model.photo = response.data;
          }
        });
      }
    },
    deleteImage() {
      this.uploadedImg = null;
      this.editedImgURL = null;
      this.model.photo.url = null;
      this.imageChenged = true;
    },
    loadCurrentUserID() {
      this.loadCurrentUser().then((response) => {
        if (response && response.status === 200) {
          this.currentUserID = response.data.id;
        }
      });
    },
    toggleEditAnswer(id) {
      let noAnswer = false;
      this.eventQuestionsAnswers.forEach((qa, index) => {
        if (qa.id === id) {
          this.eventQuestionsAnswers[index].editable = !this.eventQuestionsAnswers[index]
            .editable;
        }
        if (!qa.answer) {
          noAnswer = true;
        }
      });
      this.waitingForAnswer = noAnswer;
      this.rowsQA = [...this.eventQuestionsAnswers];
    },
    auth() {
      this.setLoadingStatus(true);
      this.makeRequest("ZOOM_AUTH", "GET").then((data) => {
        if (data.status === 200) {
          this.setLoadingStatus(false);
          window.location.replace(data.data, "_blank");
        }
      });
    },
    zoomActivate() {
      console.log("zoom");
      this.auth();
    },
    handleZoomClick() {
      this.model.meetingJoinUrl ? this.cancelMeeting() : this.createMeeting();
    },
    createMeeting() {
      this.makeRequest("MEETING_CREATE", "POST", null, this.model.id).then((result) => {
        console.log("ZOOM", result);
        if (result.status === 200) {
          if (result.data.uuid) {
            this.setLoadingStatus(false);
            this.$notify({
              group: "system",
              title: "Great!",
              text: "Zoom meeting created!",
              type: "success",
            });
            this.model.meetingJoinUrl = result.data.joinUrl;
          } else {
            // auth
            const authSuccess = this.zoomAuth();
            if (authSuccess) {
              this.createMeeting();
            }
          }
        } else {
          this.$notify({
            group: "system",
            title: "Unauthorized",
            text: "Please, connect Zoom in profile page!",
            type: "error",
          });
        }
      });
    },
    cancelMeeting() {
      this.$confirm({
        title: "Are you sure?",
        message: `You'd like to delete this Zoom conference!`,
        button: {
          no: "No",
          yes: "Cancel",
        },
        callback: (confirm) => {
          if (confirm) {
            this.makeRequest("MEETING_DELETE", "DELETE", null, this.model.id).then(
              (data) => {
                if (data.status === 200) {
                  this.setLoadingStatus(false);
                  this.$notify({
                    group: "system",
                    title: "Great!",
                    text: "Zoom meeting canceled!",
                    type: "success",
                  });
                  this.model.meetingJoinUrl = null;
                }
              }
            );
          }
        },
      });
    },
    zoomAuth() {
      console.log("zz");
      this.setLoadingStatus(true);
      return new Promise((resolve, reject) => {
        this.makeRequest("ZOOM_AUTH", "GET").then((data) => {
          if (data.status === 200) {
            this.setLoadingStatus(false);
            window.location.replace(data.data, "_blank");
            resolve();
          } else {
            reject();
          }
        });
      });
    },
    handleDates(startDate, endDate) {
      this.eventDateInterval = endDate - startDate;
    },
    handeEventEndDateChange() {
      this.inputChenged = true;
      this.endDateChangedManually = true;
      if (this.validateDateInputs()) {
        this.handleDates(this.model.startDateTime, this.model.endDateTime);
      }
      this.validateDateInputs();
    },
    setEventEndDate() {
      this.inputChenged = true;
      if (!this.endDateChangedManually && !this.model.id) {
        this.model.endDateTime = new Date(
          this.$moment(this.model.startDateTime).add(1, "hours").format()
        );
      } else {
        this.model.endDateTime = new Date(
          this.$moment(this.model.startDateTime)
            .add(this.eventDateInterval, "milliseconds")
            .format()
        );
      }
      this.validateDateInputs();
    },
    disableDate(date) {
      const startDate = this.model.startDateTime;
      return (
        startDate && date.setHours(0, 0, 0, 0) < new Date(startDate).setHours(0, 0, 0, 0)
      );
    },
    disableTime(date) {
      const startDate = this.model.startDateTime;
      return startDate && date < startDate;
    },
    disableStartDate(date) {
      let disable = false;

      const startDate = this.model.startDateTime;
      const now = new Date();

      if (
        startDate &&
        date.setHours(0, 0, 0, 0) < new Date(startDate).setHours(0, 0, 0, 0)
      ) {
        //disable = true;
      }

      if (now.setHours(0, 0, 0, 0) > date) {
        disable = true;
      }

      if (!this.model.id) {
        // DISABLE ALL DATES THAT GREATER CURRENT DATE + 30 DAYS FOR THE NEW EVENT
        if (startDate && date.setHours(0, 0, 0, 0) > now.setDate(now.getDate() + 30)) {
          disable = true;
        }
      } else {
        // DISABLE ALL DATES THAT GREATER START DATE + 30 DAYS
        if (startDate && date.setHours(0, 0, 0, 0) > date.setDate(date.getDate() + 30)) {
          disable = true;
        }
      }

      return disable;
    },
    handleResize() {
      this.windowWidth = window.innerWidth;
      if (this.windowWidth > 767) {
        this.tablesPaginationEnable = true;
      }
    },
    toggleQA(index) {
      if (this.activeToggle === index) {
        this.activeToggle = null;
      } else {
        this.activeToggle = index;
      }
    },
    validateDateInputs() {
      let isValidInputs = false;

      const startDate = this.$moment(this.model.startDateTime).format("MM/DD/YYYY HH:mm");
      const endDate = this.$moment(this.model.endDateTime).format("MM/DD/YYYY HH:mm");

      this.startDateInvalid = !this.$moment(startDate).isValid();
      this.endDateInvalid = !this.$moment(endDate).isValid();

      if (this.$moment(startDate).isValid() && this.$moment(endDate).isValid()) {
        isValidInputs = true;
      }

      return isValidInputs;
    },
    copyZoomLinkToClipboard() {
      navigator.clipboard.writeText(this.model.meetingJoinUrl);
      this.$notify({
        group: "system",
        title: "Great!",
        text: "Meeting link copied successfully!",
        type: "success",
      });
    },
    isFormChange(invalid, dirty) {
      return !(invalid || dirty || this.inputChenged || this.imageChenged);
    },
  },

  mounted() {
    var event = this.$store.getters.getEvent;
    if (event && event.id) {
      this.showPreload = true;
      this.loadEventInternal(event.id);
      this.loadCurrentUserID();

      if (this.windowWidth < 767) {
        this.tablesPaginationEnable = true;
      }
    } else {
      this.showPreload = false;
      this.model.recurrence = this.cronIntervalsTypes[0];
      this.setEventEndDate();
    }
  },
  created() {
    window.addEventListener("resize", this.handleResize);
  },
  beforeDestroy() {
    this.$store.dispatch("setActiveModal", null);
    this.$store.dispatch("setCurrentEvent", null);
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },
};
</script>

